@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: "Montserrat", sans-serif;
}

.App-logo {
  text-align: center;
  color: #fff;
}

.logo {
  height: 65px;
  width: 250px;
  margin-top: 35px;
  margin-bottom: 35px;
  padding: 25px;
}

.css-input {
  margin-bottom: 5px;
  border-width: 2px;
  border-color: #737373;
  border-style: solid;
  padding: 9px;
  border-radius: 7px;
  background-color: #505050;
  color: #ffffff;
  font-size: 18px;
}
.css-input:focus {
  outline: none;
}

.btn {
  margin: 12px;
  border-radius: 7px;
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  font-size: 22px;
  background: #3498db;
  padding: 5px 5px;
  text-decoration: none;
}

.btn:hover {
  background: #3cb0fd;
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}

.InfoBox {
  margin: 1.2rem 17rem;
  background-color: #505050;
  margin-bottom: 20px;
  border-radius: 15px;
  min-height: 558px;
  padding: 25px;
  color: #ffffff;
}

.HeadingImg {
  max-height: 300px;
  max-width: 235px;
  border-radius: 1.2rem;
}


.QuickSearch{
  color: #fff;
  position: relative;
  margin: 1.2em 17em;
  background-color: #FF3847;
  margin-bottom: 20px;
  border-radius: 15px;
  /* min-height: 558px; */
  padding: 25px;
  color: #ffffff;
}
.Result{
  position: relative;
  margin: 1.3em 5em;
  background-color: #000000;
  margin-bottom: 20px;
  border-radius: 15px;
  /* min-height: 558px; */
  padding: 10px;
  color: #ffffff;
}
